<template>
	<div>
		<vmc-hotcity v-model="show" @on-selectedcity="selectedcity"></vmc-hotcity>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				show: false
			}
		},
		methods:{
			containerStyles() {
				return { height:  document.documentElement.clientHeight * 0.8 + 'px'}
			},
			selectedcity(data) {
				this.show = false
				this.$Dialog.tip({
					message: data.name,
					title: '提示',
					confirmButtonText: '知道了',
					onOk() {
						console.log('知道了')
					}
				})
			}
		}
	}
</script>



